.footer {

    .container {
        @extend %container;
    }

    &__top-image {
        padding-bottom: 24px;

        @media screen and (min-width: 576px) {
            height: 98px;
            padding-top: 22px;
            padding-bottom: 43px;
        }

    }

    &__top-image-wrapper {
        display: flex;
        justify-content: space-between;
    }

    &__logo {
        margin-top: 6px;

        @media screen and (min-width: 576px) {
            width: 129px;
            height: 22px;
            margin-top: 10px;
        }

    }

    &__top-icon {
        width: 137px;
        //height: 26px;
        display: flex;
        justify-content: space-between;
        align-items: center;

        @media screen and (min-width: 576px) {
            margin-bottom: 7px;
        }

    }

    &__hr {
        color: $textColorLight;

        @media screen and (min-width: 576px) {
            display: none;
        }

    }

    &__top-pay {

        @media screen and (min-width: 576px) {
            display: none;
        }

    }

    &__top-pay-wrapper {
        height: 49px;
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
    }

    &__top-pay-image {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
    }

    &__top-pay-inner {
        width: 84px;
        height: 23px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__bottom {
        display: flex;
        flex-direction: column;
        align-items: center;

        @media screen and (min-width: 576px) {
            background-color: #FAF9F8;
        }

    }

    &__phone {
        margin-bottom: 10px;
    }

    &__bottom-wrapper {
        margin-top: 24px;
        margin-bottom: 20px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    &__span {
        font-size: 14px;
        font-weight: 400;
        line-height: 17.07px;
        color: $textColorLight;
    }

    &__phone-number {
        font-size: 14px;
        font-weight: 400;
        line-height: 17.07px;
        color: $textColorLight;
        text-decoration: none;
    }

    &__copyright {
        font-size: 14px;
        font-weight: 400;
        line-height: 17.07px;
        color: $textColorLight;
    }

    &__select-wrapper {
        width: 40px;
        margin-bottom: 2px;
        position: relative;

        &::after {
            content: "";
            background-image: url(./../images/photo/select-arrow.svg);
            width: 11px;
            height: 6px;
            position: absolute;
            right: 0;
            top: 7px;
        }

    }

    &__select {
        width: 100%;
        height: 19px;
        text-align: left;
        appearance: none;
        overflow: hidden;
        font-size: 15px;
        font-weight: 400;
        line-height: 18.29px;
        color: $textColor;
        border: transparent;
        outline: transparent;
        cursor: pointer;
    }

}